import * as api from '../api/api';
const redirectLogin = () => {
  window.vue.$router.replace('/login');
  window.localStorage.clear()

}

const hasLogin = () => {
  return window.localStorage.getItem('currentUser') !== null
}

const handleLogout = () => {
  api.logout()
}

export {
  redirectLogin,
  hasLogin,
  handleLogout
}



